.barba-container {
  transition: opacity 1s cubic-bezier(0.785, 0.135, 0.15, 0.86);
}

.barba-container.is-entering {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 0;
  opacity: 0;
  visibility: visible !important;
}

