}

.h2,
h2 {
  font-size: calc((24 * 1px) + (30 - 24) * ((100vw - 320px) / (1440 - 320)));
  line-height: 1.2;
}

.h3,
h3 {
  font-size: calc((20 * 1px) + (24 - 20) * ((100vw - 320px) / (1440 - 320)));
  line-height: 1.2;
}

.h4,
h4 {
  font-size: calc((16 * 1px) + (20 - 16) * ((100vw - 320px) / (1440 - 320)));
  line-height: 1.2;
}

.h5,
h5 {
  font-size: calc((16 * 1px) + (18 - 16) * ((100vw - 320px) / (1440 - 320)));
