  transition: clip-path 0.75s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  will-change: clip-path;
}

body.is-nav-open .c-nav .c-nav__wrapper {
  clip-path: circle(400vh at calc(100% - 55px) 55px);
}

