//
//
//
*******************************************************************************/

.c-page-title .c-page-title__heading {
  margin: 160px 0 0;
  margin: 10rem 0 0;
  display: inline-block;
}

.c-page-title .c-page-title__heading .c-page-title__heading-text {
  margin: 0;
  color: #fff;
  font-family: "Code Sans", Arial, Helvetica, sans-serif;
  font-size: calc(60px + (72 - 60) * ((100vw - 320px) / (1440 - 320)));
  line-height: 1;
}

.c-page-title .c-page-title__heading .c-page-title__heading-text.-green-blue {
  background: linear-gradient(135deg, rgba(15,255,140,1) 0%, rgba(41,250,240,1) 25%, rgba(15,255,140,1) 50%, rgba(41,250,240,1) 100%);
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
}

.c-page-title .c-page-title__heading .c-page-title__heading-text.-purple-orange {
  background: linear-gradient(135deg, rgba(214,54,252,1) 0%, rgba(255,204,64,1) 25%, rgba(214,54,252,1) 50%, rgba(255,204,64,1) 100%);
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
}

/*******************************************************************************
//
//
//
//  #PAGINATION
//
//
//
*******************************************************************************/

.c-pagination {
  display: flex;
  justify-content: space-between;
  padding-top: 30px;
  margin-top: 60px;
  border-top: 2px solid #0FFF8C;
}

.c-pagination > a {
  display: flex;
  align-items: center;
  position: relative;
  color: #0FFF8C;
  font-size: 12px;
  font-weight: 700;
  letter-spacing: 4px;
  text-transform: uppercase;
  transition: color 0.5s cubic-bezier(0.785, 0.135, 0.15, 0.86);
}

.c-pagination > a svg {
  fill: #0FFF8C;
  transition: fill 0.5s cubic-bezier(0.785, 0.135, 0.15, 0.86);
}

.c-pagination > a .c-pagination__text {
  margin: 0 12px;
}

.c-pagination > a.c-pagination__next {
  margin-left: auto;
}

.c-pagination > a:hover {
  color: #fff !important;
}

.c-pagination > a:hover svg {
  fill: #fff !important;
}

.c-pagination.-black {
  border-color: #030D11;
}

.c-pagination.-black > a {
  color: #030D11;
}

.c-pagination.-black > a svg {
  fill: #030D11;
}

.c-pagination.-blue {
  border-color: #29FAFF;
}

.c-pagination.-blue > a {
  color: #29FAFF;
}

.c-pagination.-blue > a svg {
  fill: #29FAFF;
}

.c-pagination.-green {
  border-color: #0FFF8C;
}

.c-pagination.-green > a {
  color: #0FFF8C;
}

.c-pagination.-green > a svg {
  fill: #0FFF8C;
}

.c-pagination.-purple {
  border-color: #D636FC;
}

.c-pagination.-purple > a {
  color: #D636FC;
}

.c-pagination.-purple > a svg {
  fill: #D636FC;
}

.c-pagination.-yellow {
  border-color: #FFCC40;
}

.c-pagination.-yellow > a {
  color: #FFCC40;
}

.c-pagination.-yellow > a svg {
  fill: #FFCC40;
}

.c-pagination.-white {
  border-color: #fff;
}

.c-pagination.-white > a {
  color: #fff;
}

.c-pagination.-white > a svg {
  fill: #fff;
}

